body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background :hsla(96, 82%, 52%, 0.795) ;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  box-sizing: border-box;
  border-radius: 30px;
}
h2{
  background-color: rgb(78, 146, 211);
  padding: 18px;
  color: #fff;
  text-align: center;
}
.App{
  padding: 30px;
  background: #fff;
  width: 80%;
  margin: 80px auto;
  box-shadow: 1px 2px 5px #eee;
}
input {
  width: 80%;
  border: 0;
  padding:  6px 10px;
  background: #c8c8c8;
}
button{
  width: 20%;
  border: 0;
  padding: 6px 0;
  margin: 0;
  outline: 0;
  color:#fff;
  font-weight: bold;
  cursor: pointer;
  background: #4dc55b;
}
ul{
  list-style: none;
  padding: 0;
}
li{
  margin: 15px 0;
}
li span {
  display: inline-block;
  width: 60%;
  color: #0c0c0c;
}
li > button:first-of-type{
  background: #6a6a6a;
  color: #fff;
  margin-right: 1%;
  width: 19%; 
}
li > button:last-of-type{
  background: #d9534f;
  color: #fff;
}
ul > form {
  margin: 15px 0;
}

